.imgBox {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 30vw;
    width: 40vw;
}

#logo-img {
    display: flex;
    height: 120px;
}

@media (min-width: 600px) {

    .imgBox {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 45vh;
        width: 30vw;
    }

    #logo-img {
        display: flex;
        height: 320px;
    }
}