.videoBox {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    min-width: 20vw;
    min-height: 20vh;
}

iframe {
    width: 200px;
}

@media (min-width: 600px) {
    .videoBox {
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 40vw;
        min-height: 20vh;
    }
    
    iframe {
        width:400px;
        height:320px;
    }
}